* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

a {
  color: white;
}

body {
  font-family: "Poppins", sans-serif;
  justify-content: center;
  align-items: center;

}





/* Helpers */
.container {
  width: 1200px;
  margin: 0 auto;
}

.h-650 {
  height: 650px;
}
.alignCenter {
  align-items: center;
}
.justifyConter {
  justify-content: center;
}

/* Grid System */
.row {
  display: flex;
  width: 100%;
  height: 50%;
  flex-wrap: wrap;
}
.col-6 {
  width: 50%;
}

.col-4 {
  width: 33.33333333333333%;
}
.underline {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: orange;
          text-decoration-color: orange;
  text-decoration-thickness: 5px;
}

/* header */




/* Servives */
.services {
  /* background: #1f2235; */
  padding: 100px 0;
}

.common {
  width: 33.33333333333333%;
  margin: 20px auto;
  text-align: center;
}

.heading {
  margin-bottom: 20px;
  font-weight: 500;
  color: #ff4a57;
  font-size: 16px;
}
.mainHeader {
  font-size: 32px;
  margin-bottom: 20px;
}
.mainContent {
  font-size: 16px;
}
.commonBorder {
  background: #ff4a57;
  height: 3px;
  width: 100px;
  margin: 30px auto;
}

.services__box {
  padding: 40px;
  margin: 20px;
  transition: all 0.5s linear;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  background: rgba(255, 255, 255, 0.1);

}
.services__box:hover {
  background: rgba(255, 255, 255, 0.1);
}

.commonIcons {
  font-size: 40px;
  color: #f00e06;
  margin-bottom: 15px;
}


.services__box-header {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 15px;
}

.services__box-p {
  font-size: 14px;
}



/* Prices */

.prices {
  width: 100%;
  padding: 100px 0;
}
.price {
  padding: 25px;
  border-radius: 8px;
  margin: 35px 20px;
  text-align: center;
}
.priceHeading {
  display: block;
  height: 100%;
  width: 100%;
  /* shape-image-threshold: calc(); */
}

.price__rs {
  font-size: 70px;
  font-weight: 200;
  margin-bottom: 15px;
}

.price__rs span {
  font-size: 25px;
}
.price ul {
  list-style-type: none;
}
.price ul li {
  margin: 5px 0;
  border-bottom: 1px solid #3c3c3c;
  font-size: 14px;
  padding: 15px 0;
}
.price__btn {
  margin-top: 40px;
}


.contact {
  padding: 100px 0;
}
.contactSection-logo {
  text-align: center;
  margin-bottom: 20px;
}
.contactSection-logo img {
  margin: 0 auto;
  width: 130px;
}
.contactSection p {
  font-size: 14px;
  margin-bottom: 20px;
}

.contactCircles {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: center;
}
.contactCircles li {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  transition: all 0.5s linear;
  cursor: pointer;
}
.contactCircles li:hover {
  background: #ff4a57;
}


.user-icon {
  font-size: 1.6rem;
  color: silver;
}

